// **************************************
//* About Global Variables
// **************************************




// **************************************
//* About Shape
// **************************************

// square
@mixin square($d) {
    width: $d;
    height: $d; }


@mixin rect($long, $width) {
    width: $long;
    height: $width; }


// circle
@mixin circle($d) {
    width: $d;
    height: $d;
    border-radius: 50%; }


// gold rect
@mixin goldrect($d) {
    width: $d;
    height: 1.618033 * $d; }




// **************************************
//* About Position
// **************************************

// margin center
@mixin center {
    margin: 0 auto; }


// abosolute position center
@mixin abscenter {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%); }


// flex position center
@mixin flexcenter {
    display: flex;
    align-items: center;
    justify-content: center; }

@mixin  inline($d) {
    line-height: $d;
    height: $d; }




@mixin cover {
    width: 100%;
    height: 100%; }
@mixin center {
    margin: 0 auto; }

@mixin square($d) {
    width: $d;
    height: $d; }


//***********************************
//  * About others
//************************************

@mixin after {
    content: '';
    position: absolute; }


@mixin focus {
    outline: none; }

@mixin aback {
    text-decoration: underline; }

@mixin pointer {
    cursor: pointer; }

@mixin overlay($opacity) {
    background: rgba(0,0,0,$opacity); }



// clear input or button
@mixin btnclear {
    border: 0;
    cursor: pointer;
    &:focus,
    &:active {
        outline: 0;
        border: 0; } }
@mixin  inputclear {
    outline: 0;
    border-radius: 0;
    border: 1px solid #000;
    &:focus,
    &:active {
        outline: 0; } }


@mixin fourzero {
    top: 0;
    left: 0;
    right: 0;
    bottom: 0; }
